






import { Component, Vue } from "vue-property-decorator";
import MyCanvas from "@/libs/canvas";

@Component({
  components: {}
})
export default class GrayScale extends Vue {
  async mounted() {
    const canvas = new MyCanvas();
    const resp = await canvas.lennner(".main_canvas");
    if (!resp) return;

    for (let y = 0; y < resp.height; y++) {
      for (let x = 0; x < resp.width; x++) {
        const base = (y * resp.width + x) * 4;
        const medColor =
          (Math.max(resp.pixels[base + 0], resp.pixels[base + 1], resp.pixels[base + 2]) +
            Math.min(resp.pixels[base + 0], resp.pixels[base + 1], resp.pixels[base + 2])) /
          2;
        resp.pixels[base + 0] = medColor;
        resp.pixels[base + 1] = medColor;
        resp.pixels[base + 2] = medColor;
        resp.pixels[base + 3] = 255;
      }
    }
    resp.context.putImageData(resp.imgData, 0, 0);
  }
}
